"use client";

import * as React from "react";

import { cn } from "@/lib/utils";

type TextAnimateAnimation = "blurIn" | "blurInUp";
type TextAnimateBy = "character" | "word" | "line";

type TextAnimateProps<TAs extends React.ElementType> = {
  as?: TAs;
  animation?: TextAnimateAnimation;
  by?: TextAnimateBy;
  once?: boolean;
  className?: string;
  children: React.ReactNode;
  style?: React.CSSProperties;
  /** Delay in ms between items when using `by` */
  staggerMs?: number;
} & Omit<
  React.ComponentPropsWithoutRef<TAs>,
  "as" | "children" | "className" | "style"
>;

function splitText(text: string, by: TextAnimateBy): Array<string> {
  if (by === "character") return Array.from(text);
  if (by === "word") return text.split(/(\s+)/);
  return text.split(/(\n)/);
}

export function TextAnimate<TAs extends React.ElementType = "span">({
  as,
  animation = "blurIn",
  by,
  once = false,
  className,
  children,
  style,
  staggerMs = 18,
  ...props
}: TextAnimateProps<TAs>) {
  const Comp = (as ?? "span") as React.ElementType;
  const rootRef = React.useRef<HTMLElement | null>(null);
  const [isVisible, setIsVisible] = React.useState(false);

  React.useEffect(() => {
    const el = rootRef.current;
    if (!el) return;

    const observer = new IntersectionObserver(
      (entries) => {
        const entry = entries[0];
        if (!entry) return;

        if (entry.isIntersecting) {
          setIsVisible(true);
          if (once) observer.disconnect();
        } else if (!once) {
          setIsVisible(false);
        }
      },
      { threshold: 0.2 }
    );

    observer.observe(el);
    return () => observer.disconnect();
  }, [once]);

  const canSplit = typeof children === "string" && !!by;
  const tokens = canSplit ? splitText(children as string, by!) : null;
  const isWhole = !tokens;

  return (
    <Comp
      ref={rootRef as unknown as React.Ref<any>}
      className={cn("magicui-text-animate", isWhole && "magicui-text-whole", className)}
      data-animation={animation}
      data-visible={isVisible ? "true" : "false"}
      style={style}
      {...props}
    >
      {tokens ? (
        <>
          <span className="sr-only">{children}</span>
          <span aria-hidden="true">
            {tokens.map((token, i) => {
              if (by === "line" && token === "\n") {
                return <br key={`br-${i}`} />;
              }

              const renderedToken = token === " " ? "\u00A0" : token;
              const isWhitespace = /^\s+$/.test(token);

              return (
                <span
                  key={`${token}-${i}`}
                  className={cn(
                    "magicui-text-item",
                    isWhitespace && "magicui-text-whitespace"
                  )}
                  style={{
                    animationDelay: `${i * staggerMs}ms`,
                  }}
                >
                  {renderedToken}
                </span>
              );
            })}
          </span>
        </>
      ) : (
        children
      )}
    </Comp>
  );
}
